.growtype-form-wrapper input::-webkit-outer-spin-button,
.growtype-form-wrapper input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}
.growtype-form-wrapper .form-check {
  padding-left: 30px;
  margin: 0;
  min-height: 23px;
}
.growtype-form-wrapper .form-check .form-check-input {
  min-height: 19px;
  position: relative;
  top: 2px;
  width: 19px;
  margin: 0;
  margin-left: -29px;
  min-width: 19px;
  cursor: pointer;
  height: 19px;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  float: left;
}
.growtype-form-wrapper .form-check .form-check-input:focus {
  outline: none;
  box-shadow: none;
}
.growtype-form-wrapper .form-check .form-check-input:before {
  content: "";
  background: #f0f0f0;
  border: 1px solid #e6e6e6;
  bottom: -1px;
  color: #fff;
  font-size: 19px;
  left: -1px;
  position: absolute;
  right: -1px;
  top: -1px;
}
.growtype-form-wrapper .form-check .form-check-input:checked:before {
  bottom: 0;
  color: black;
  content: "\f15e";
  font-family: dashicons;
  font-size: 19px;
  left: 0;
  line-height: 18px;
  position: absolute;
  right: 0;
  text-indent: -1px;
  top: 0;
}
.growtype-form-wrapper input[type=number] {
  -moz-appearance: textfield;
  /* Firefox */
}
.growtype-form-wrapper input[type=checkbox] {
  cursor: pointer;
}
.growtype-form-wrapper input:not([type=checkbox]):not([type=radio]):not([type=submit]):not(.qty), .growtype-form-wrapper textarea {
  min-height: 48px;
  padding: 10px 15px;
  width: 100%;
}
.growtype-form-wrapper .chosen-container-single .chosen-single abbr {
  top: 15px;
}
.growtype-form-wrapper .e-wrapper {
  position: relative;
}
.growtype-form-wrapper .e-wrapper .input-icon {
  position: absolute;
  right: 20px;
  top: 42px;
  opacity: 0.7;
  pointer-events: none;
}
.growtype-form-wrapper .e-wrapper[data-label=false] .input-icon {
  top: 12px;
}
.growtype-form-wrapper .input-file-wrapper {
  position: relative;
}
.growtype-form-wrapper .input-file-wrapper .dashicons-media-document {
  font-size: 70px;
  width: 70px;
  height: 70px;
}
.growtype-form-wrapper .bootstrap-filestyle .woocommerce form .form-row input.input-text, .growtype-form-wrapper .bootstrap-filestyle .woocommerce form .form-row textarea, .growtype-form-wrapper .bootstrap-filestyle input:not([type=checkbox]):not([type=radio]):not([type=submit]):not(.qty), .growtype-form-wrapper .bootstrap-filestyle textarea {
  width: initial;
}
.growtype-form-wrapper .bootstrap-filestyle .group-span-filestyle {
  z-index: 1;
}
.growtype-form-wrapper .bootstrap-filestyle .form-control {
  background: none !important;
  border: none !important;
}
.growtype-form-wrapper .radio-wrapper input[type=radio] {
  cursor: pointer;
  margin-right: 6px;
}
.growtype-form-wrapper .radio-wrapper label {
  cursor: pointer;
}
.growtype-form-wrapper .radio-wrapper label a {
  font-size: 14px;
}
.growtype-form-wrapper .form-label-placeholder {
  background: white;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
  color: #999fa5;
  pointer-events: none;
  padding: 14px;
  font-size: 15px;
  font-weight: 400;
  line-height: 19px;
  border-radius: 4px;
  border: 1px solid #aaaaaa;
}
.growtype-form-wrapper label.error-label, .growtype-form-wrapper label.error {
  margin-top: 5px;
  font-size: 14px;
}
.growtype-form-wrapper .chosen-container-single .chosen-single {
  height: 48px !important;
}
.growtype-form-wrapper .chosen-container-single .chosen-single span {
  min-height: 45px;
}
.growtype-form-wrapper .chosen-container-single .chosen-single div b {
  top: 11px;
}
.growtype-form-wrapper .chosen-single {
  padding-left: 15px;
}
.growtype-form-wrapper .chosen-container-multi .chosen-choices {
  height: 48px !important;
  border-radius: 3px !important;
}
.growtype-form-wrapper .chosen-container-multi .chosen-choices .chosen-search-input {
  min-height: 47px !important;
  margin: 0 !important;
  font-size: 15px !important;
  padding-left: 9px !important;
}

.growtype-form-wrapper input:not([type=checkbox]):not([type=radio]):not([type=submit]):not(.qty)[type=file] {
  padding-top: 13px;
}

.growtype-form-wrapper .btn-google-login {
  border-radius: 0.375rem;
  gap: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #d1d5db;
  font-size: 14px;
  background: black;
  color: white;
  min-height: 42px;
}
.growtype-form-wrapper .btn-google-login img {
  position: relative;
  top: 1px;
}

.growtype-form-avatar .growtype-form-avatar-inner {
  display: flex;
}
.growtype-form-avatar .growtype-form-avatar-image {
  border-radius: 100px;
  overflow: hidden;
  max-width: 70px;
}
.growtype-form-avatar .growtype-form-avatar-details {
  padding-left: 20px;
  display: flex;
  align-items: center;
}
.growtype-form-avatar .growtype-form-avatar-details .e-title {
  font-size: 21px;
  font-weight: 600;
}